import React, {useState} from 'react';
import ReactPlayer from 'react-player';
import styles from './Hall.module.scss';
import rocet from '../../image/Hall/rocet.png'
import helix from '../../image/Hall/helix.png'

export default function Hall () {

    const [isVideo, setIsVideo] = useState('https://www.youtube.com/embed/bLwQnK6xplI');

    const handleOne = () => {
        setIsVideo('https://youtu.be/TG17LYvc7x4')
    };

    const handleTwo = () => {
        setIsVideo('https://youtu.be/r1QILqwzs1M')
    };

    const handleThree = () => {
        setIsVideo('https://youtu.be/B9Rmj2IvKgg')
    };

    const handleFour = () => {
        setIsVideo('https://youtu.be/5pKfbBJ1Jqw')
    };

    const  handleFive = () => {
        setIsVideo('https://youtu.be/ZsncR702gZs')
    }; 

    return (
        <section className={styles.main} id="Hall">
            <img className={styles.helix} src={helix} alt=''/>
            <div className={styles.box}>
                <img className={styles.rocet} src={rocet} alt=''/>
                    <div className={styles.info}>
                        <h2 className={styles.title}> </h2>
                        <p className={styles.subtitle}>  2.0. </p>
                        <p className={styles.text}> 25 , 11:00 ()</p>
                    </div>
                    <div className={styles.box_video}>
                        <ReactPlayer 
                            width='100%'
                            height='100%'
                            className={styles.video} url={isVideo}/>
                    </div>
                    <ul className={styles.list} >
                    <li className={styles.item}>
                        <button className={styles.button} type="button" onClick={handleOne}> 1. </button>
                    </li>
                    <li className={styles.item}>
                        <button className={styles.button} type="button" onClick={handleTwo}> 2. </button>
                    </li>
                    <li className={styles.item}>
                        <button className={styles.button} type="button" onClick={handleThree}> 3. </button>
                    </li>
                    <li className={styles.item}>
                        <button className={styles.button} type="button" onClick={handleFour}> 4. </button>
                    </li>
                    <li className={styles.item}>
                        <button className={styles.button} type="button" onClick={handleFive}> 5. </button>
                    </li>
                </ul>
            </div>
        </section>
    )
}
