import React from 'react';
import styles from './InitialPage.module.scss';
import hat from '../../image/InitialPage/hat.png';
import helix from '../../image/InitialPage/Helix.png'
import cone from '../../image/InitialPage/Cone.png'
import icosahedron from '../../image/InitialPage/Icosahedron.png'

export default function InitialPage () {
    return (
    <section className={styles.main}>
        <img className={styles.icosahedron} src={icosahedron} alt=''/>
        <div className={styles.container}>
            <div className={styles.image} >
                <img className={styles.cone} src={cone} alt=''/>
                <img className={styles.helix} src={helix} alt=''/>
                <img className={styles.hat} src={hat} alt=''/>
            </div>
            <div className={styles.box}>
                <h3 className={styles.subtitle}>  2.0</h3>
                <h1 className={styles.title}>   &#160; </h1>
                <p className={styles.text}>  25   11:00 ()</p>
            </div>
        </div>
    </section>
    )
}