import React from 'react';
import styles from './Club.module.scss';
import icosahedron from '../../image/InitialPage/Icosahedron.png'
import helix from '../../image/AboutUs/helix.png';
import image from '../../image/Club/triangle.png';

export default function Club () {


    return (
        <section className={styles.main} id="Club">
            <div className={styles.container}>
                <img className={styles.icosahedron} src={icosahedron} alt=''/>
                <img className={styles.icosahedron_transparent} src={icosahedron} alt=''/>
                <img className={styles.helix} src={helix} alt=''/>
                <img className={styles.triangle} src={image} alt=''/>
                <div className={styles.box}>
                    <div className={styles.box_info}>
                        <h2 className={styles.title}>  HRTech</h2>
                        <p className={styles.text}>    HRTech  ,         .        .    HRTech  </p> 
                        <p className={styles.text}>       <span className={styles.text_blue}>         .</span>
                        </p>
                        <a className={styles.link} target='_blank' rel='noopener noreferrer' href='https://vk.com/actionhrtech'>  </a>
                    </div>
                </div>
            </div>
        </section>
    )
}